
import { defineComponent, computed, onMounted, ref } from "vue";
import gsap from "gsap";
import { useDataStore } from "../store/main";

export default defineComponent({
  setup() {
    const store = useDataStore();
    const routes = computed(() => [
      { id: 0, path: "/", name: "Home" },
      { id: 1, path: "/work", name: "Projects" },
    ]);
    const setIndexValue = ref<number | null>(null);
    const handleMouseDown = (index: number) => {
      setIndexValue.value = index;
    };
    const handleMouseUp = () => {
      setIndexValue.value = null;
    };
    const routeStyle = (index: number) => {
      if (index === setIndexValue.value) {
        return {
          backgroundColor: "#063DCC",
          color: "white",
          borderRadius: "25px",
          border: "1px solid #063DCC",
        };
      }
    };

    onMounted(() => {
      gsap.to(".router-link", {
        y: 20,
        opacity: 1,
        duration: 0.6,
        stagger: 0.2,
      });
      gsap.to(".pp-logo", {
        duration: 1,
        opacity: 1,
        top: 20,
      });
    });

    return {
      routeStyle,
      handleMouseDown,
      handleMouseUp,
      routes,
      store,
    };
  },
});
