
import { defineComponent, onMounted, ref } from "vue";
import { useDataStore } from "../store/main.js";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import gsap from "gsap";
import Cursor from "@/components/CustomCursor.vue";
import ProjectCard from "@/components/ProjectCards.vue";
import { useRouter } from "vue-router";

export default defineComponent({
  components: {
    Cursor,
    ProjectCard,
  },
  setup() {
    const homeElements = ref<HTMLElement[]>([]);
    const projects = ref<HTMLElement[]>([]);
    gsap.registerPlugin(ScrollTrigger);
    const store = useDataStore();
    const router = useRouter()

    // Fonction pour faire défiler la page vers le haut
    const scrollToTop = () => {
      window.scrollTo({
        top: 0,
        behavior: "smooth", // Ajoute un effet de défilement en douceur
      });
    };

    onMounted(async () => {
      await store.getDataFromFirebase();
      router.beforeEach((to, from, next) => {
        // Faites défiler la page vers le haut avant de naviguer
        scrollToTop();
        next();
      });
      
      //GSAP Animation sur le h1 du haut de page //

      homeElements.value = gsap.utils.toArray(".presentation span");
      homeElements.value.forEach((span: HTMLElement, index: number) => {
        gsap.to(span, {
          opacity: 1,
          duration: 1.5,
          delay: index * 0.3,
          transform: "translateY(0)",
        });
        gsap.to(".presentation-other-skills", {
          opacity: 1,
          duration: 4,
        });
      });

      const tl = gsap.timeline();
      // -- GSAP Animations -- //
      tl.to(".presentation-other-skills-round", {
        rotate: "720deg",
        duration: 2,
        scrollTrigger: {
          trigger: ".presentation",
          start: "top top",
          end: "+=600",
          scrub: true,
        },
      });
      tl.to(".picture-skills", {
        borderRadius: 20,
        marginLeft: "5vw",
        width: "15vw",
        opacity: 1,
        duration: 2,
        scrollTrigger: {
          trigger: ".presentation",
          start: "top top",
          end: "+=600",
          scrub: true,
        },
      });
      tl.to(".picture-presentation", {
        opacity: 1,
        duration: 0.5,
        transform: "translateX(0)",
        scrollTrigger: {
          trigger: ".presentation-other-skills",
          start: "top top",
          end: "+=200",
          scrub: true,
        },
      });
      tl.to(".project-title", {
        duration: 4,
        opacity: 1,
        transform: "translateX(0)",
        scrollTrigger: {
          trigger: ".picture",
          start: "center center",
          end: "+=500",
          scrub: true,
        },
      });
      tl.to(".projects", {
        duration: 1,
        transform: "translateY(-100px)",
        scrollTrigger: {
          trigger: ".project-title",
          start: "bottom bottom",
          end: "+=1000",
          scrub: true,
        },
      });

      tl.to(".project-techno", {
        rotate: "720deg",
        duration: 2,
        scrollTrigger: {
          trigger: ".project-title",
          start: "bottom bottom",
          end: "+=1000",
          scrub: true,
        },
      });

      projects.value = gsap.utils.toArray(".project-card");
      projects.value.forEach((project) => {
        tl.to(project, {
          opacity: 1,
          duration: 60,
          scrollTrigger: {
            trigger: project,
            start: "top center",
            end: "top top",
            scrub: true,
          },
        });
      });
    });

    return {
      store,
      scrollToTop,
    };
  },
});
